import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { loadGames, loadMore } from "../actions/gamesAction";
import Game from "../components/Game";
import styled from "styled-components";
import { motion, AnimatePresence, AnimateSharedLayout } from "framer-motion";
import GameDetail from "../components/GameDetail";
import { useLocation } from "react-router-dom";

function Home() {
  const [number, setNumber] = useState(2);
  const [lessData, setLessData] = useState(number - 1);
  //get current location
  const location = useLocation();
  const pathId = location.pathname.split("/")[2];
  console.log(pathId);

  //fetching data
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(loadGames());
  }, [dispatch]);

  //get the data
  const { popular, newGames, upcoming, searched, popularMore } = useSelector(
    (state) => state.games
  );

  const moreCount = () => {
    setNumber(number + 1);
    dispatch(loadMore(number));
  };

  return (
    <GameList>
      <AnimateSharedLayout type="crossfade">
        <AnimatePresence>
          {pathId && <GameDetail pathId={pathId} />}
        </AnimatePresence>
        {searched.length ? (
          <div>
            <h2>Searched Games</h2>
            <Games>
              {searched.map((game) => (
                <Game
                  name={game.name}
                  released={game.released}
                  id={game.id}
                  key={game.id}
                  image={game.background_image}
                />
              ))}
            </Games>
          </div>
        ) : (
          ""
        )}
        <h2>Upcoming Games</h2>
        <Games>
          {upcoming.map((game) => (
            <Game
              name={game.name}
              released={game.released}
              id={game.id}
              key={game.id}
              image={game.background_image}
            />
          ))}
        </Games>
        <h2>Popular Games</h2>
        <Games>
          {popular.map((game) => (
            <Game
              name={game.name}
              released={game.released}
              id={game.id}
              key={game.id}
              image={game.background_image}
            />
          ))}
        </Games>
        {popularMore.length ? (
          <Games>
            {popularMore.map((game) => (
              <Game
                name={game.name}
                released={game.released}
                id={game.id}
                key={game.id}
                image={game.background_image}
              />
            ))}
          </Games>
        ) : (
          ""
        )}
        <button onClick={moreCount}>More</button>
        <h2>New Games</h2>
        <Games>
          {newGames.map((game) => (
            <Game
              name={game.name}
              released={game.released}
              id={game.id}
              key={game.id}
              image={game.background_image}
            />
          ))}
        </Games>
      </AnimateSharedLayout>
    </GameList>
  );
}

//styles
const GameList = styled(motion.div)`
  padding: 0 5rem 5rem 5rem;
  h2 {
    padding: 5rem 0rem;
  }
  @media (max-width: 666px) {
    padding: 0 0.5rem 5rem 0.5rem;
  }
`;

const Games = styled(motion.div)`
  min-height: 80vh;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(500px, 1fr));
  row-gap: 5rem;
  column-gap: 2rem;

  @media (max-width: 666px) {
    grid-template-columns: 1fr;
  }
`;
const Games2 = styled(motion.div)`
  min-height: 80vh;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(500px, 1fr));
  row-gap: 5rem;
  column-gap: 2rem;
`;

export default Home;
