const base_url = "https://api.rawg.io/api/";

//date
const getCurrentMonth = () => {
  const month = new Date().getMonth() + 1;
  if (month < 10) {
    return `0${month}`;
  } else {
    return month;
  }
};

const getCurrentDay = () => {
  const day = new Date().getDay();
  if (day < 10) {
    return `0${day}`;
  } else {
    return day;
  }
};

const CurrentYear = new Date().getFullYear();
const CurrentMonth = getCurrentMonth();
const CurrentDay = getCurrentDay();
const CurrentDate = `${CurrentYear}-${CurrentMonth}-${CurrentDay}`;
const lastYear = `${CurrentYear - 1}-${CurrentMonth}-${CurrentDay}`;
const nextYear = `${CurrentYear + 1}-${CurrentMonth}-${CurrentDay}`;

//popular games
const popular_games = `games?dates=${lastYear},${CurrentDate}&ordering=-rating&page_size=12`;
const upcoming_games = `games?dates=${CurrentDate},${nextYear}&ordering=-added&page_size=12`;
const new_games = `games?dates=${lastYear},${CurrentDate}&ordering=-released&page_size=12`;

export const popularGamesUrl = () => `${base_url}${popular_games}`;
export const upcomingGamesUrl = () => `${base_url}${upcoming_games}`;
export const newGamesUrl = () => `${base_url}${new_games}`;
export const gameDetailUrl = (game_id) => `${base_url}games/${game_id}`;
export const gamesScreenShots = (game_id) =>
  `${base_url}games/${game_id}/screenshots`;
export const SearchedApiUrl = (game_name) =>
  `${base_url}games?search=${game_name}&page_size=9`;

export const MorePopular = (count) =>
  `${base_url}games?dates=${lastYear},${CurrentDate}&ordering=-rating&page=${count}`;
