import axios from "axios";
import {
  popularGamesUrl,
  newGamesUrl,
  upcomingGamesUrl,
  SearchedApiUrl,
  MorePopular,
} from "../api";

export const loadGames = () => async (dispatch) => {
  const popularData = await axios.get(popularGamesUrl());
  const upcomingData = await axios.get(upcomingGamesUrl());
  const newGamesData = await axios.get(newGamesUrl());
  dispatch({
    type: "FETCH_GAMES",
    payload: {
      popular: popularData.data.results,
      new: newGamesData.data.results,
      upcoming: upcomingData.data.results,
    },
  });
};

export const searchedGames = (game_name) => async (dispatch) => {
  const searchedData = await axios.get(SearchedApiUrl(game_name));
  dispatch({
    type: "SEARCHED_GAMES",
    payload: {
      searched: searchedData.data.results,
    },
  });
};

export const loadMore = (count) => async (dispatch) => {
  const loadMoreData = await axios.get(MorePopular(count));
  dispatch({
    type: "LOAD_MORE",
    payload: {
      popularMore: loadMoreData.data.results,
    },
  });
};
