import React, { useState } from "react";
import logo from "../img/logo.svg";
import styled from "styled-components";
import { motion } from "framer-motion";
//redux
import { searchedGames } from "../actions/gamesAction";
import { useDispatch } from "react-redux";

function Nav() {
  const dispatch = useDispatch();
  const [textInput, setTextInput] = useState("");
  const inputHandler = (e) => {
    setTextInput(e.target.value);
  };
  const buttonHandler = (e) => {
    e.preventDefault();
    dispatch(searchedGames(textInput));
    setTextInput("");
  };
  const clearSearch = () => {
    dispatch({ type: "CLEAR_SEARCHED" });
  };
  return (
    <StyledNav>
      <LogoWrapper onClick={clearSearch}>
        <h2>Ignite</h2>
        <img src={logo} alt="ignite logo" />
      </LogoWrapper>
      <StyledForm>
        <input
          value={textInput}
          onChange={inputHandler}
          type="text"
          name="search"
        />
        <button onClick={buttonHandler}>
          <label htmlFor="search">Search</label>
        </button>
      </StyledForm>
    </StyledNav>
  );
}

const StyledNav = styled.section`
  min-height: 10vh;
  padding: 3rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
`;
const LogoWrapper = styled(motion.div)`
  display: flex;
  align-items: center;
  margin-bottom: 1rem;
  cursor: pointer;
  h2 {
    font-size: 1.6rem;
    margin-right: 1rem;
  }
  img {
    width: 2rem;
  }
`;
const StyledForm = styled(motion.form)`
  display: flex;
  input {
    border: 1px solid gray;
    border-right: none;
    padding: 0.5rem 1rem;
    outline: none;
    transition: 0.5s all ease;
    font-weight: bold;
    font-family: sans-serif;
    font-size: 1.2rem;
    &:focus {
      box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
    }
    &:focus + button {
      background: #506350;
    }
  }
  button {
    border: none;
    padding: 0.5rem 1rem;
    cursor: pointer;
    background: cornflowerblue;
  }
  label {
    pointer-events: none;
    font-size: 1rem;
    color: white;
  }
`;
export default Nav;
